import { STORAGE_KEYS } from '../constants/defaultData';

// localStorage    
export const loadFromStorage = <T,>(key: string, defaultValue: T): T => {
  try {
    const stored = localStorage.getItem(key);
    if (stored) {
      const parsed = JSON.parse(stored);

      //    categories  
      if (key === STORAGE_KEYS.PAGES && Array.isArray(parsed)) {
        return parsed.map((page: any) => ({
          ...page,
          categories: (page.categories || []).map((category: any) => ({
            ...category,
            connections: category.connections || [] // connections  
          }))
        })) as T;
      }

      return parsed;
    }
  } catch (error) {
    console.error(`localStorage   (${key}):`, error);
  }
  return defaultValue;
};

// localStorage  
export const saveToStorage = (key: string, data: any): void => {
  try {
    localStorage.setItem(key, JSON.stringify(data));
  } catch (error) {
    console.error(`localStorage   (${key}):`, error);
  }
};
