import React, { useEffect, useState } from 'react';
import { TutorialStep } from '../types';

interface TutorialProps {
  steps: TutorialStep[];
  currentStep: number;
  onNext: () => void;
  onSkip: () => void;
  onComplete: () => void;
  canProceed?: boolean; // validation  
}

export const Tutorial: React.FC<TutorialProps> = ({
  steps,
  currentStep,
  onNext,
  onSkip,
  onComplete,
  canProceed = true
}) => {
  const [highlightPosition, setHighlightPosition] = useState<DOMRect | null>(null);
  const step = steps[currentStep];

  useEffect(() => {
    if (!step) return;

    //     
    if (step.targetElement) {
      const element = document.querySelector(step.targetElement);
      if (element) {
        const rect = element.getBoundingClientRect();
        setHighlightPosition(rect);
      }
    } else {
      setHighlightPosition(null);
    }
  }, [step]);

  if (!step) return null;

  const isLastStep = currentStep === steps.length - 1;

  //    (    )
  const interactiveCanvasSteps = ['canvas-pan', 'canvas-zoom', 'add-memo', 'memo-drag', 'connections', 'disconnect-mode', 'categories', 'category-area', 'undo-redo'];
  const isInteractiveStep = interactiveCanvasSteps.includes(step.id);

  //    (   )
  const getTooltipStyle = (): React.CSSProperties => {
    const padding = 20;
    const tooltipWidth = 400;

    //     
    if (isInteractiveStep) {
      return {
        position: 'fixed',
        top: `${padding}px`,
        right: `${padding}px`,
        zIndex: 10002,
        maxWidth: `${tooltipWidth}px`
      };
    }

    if (!highlightPosition) {
      return {
        position: 'fixed',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        zIndex: 10002,
        maxWidth: `${tooltipWidth}px`
      };
    }

    const position = step.position || 'bottom';

    //   
    let left = 0;
    let top = 0;
    let transform = '';

    if (position === 'center') {
      // center   
      return {
        position: 'fixed',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        zIndex: 10002,
        maxWidth: `${tooltipWidth}px`
      };
    }

    switch (position) {
      case 'top':
        left = Math.max(padding + tooltipWidth / 2, Math.min(window.innerWidth - padding - tooltipWidth / 2, highlightPosition.left + highlightPosition.width / 2));
        top = Math.max(padding, highlightPosition.top - 20);
        transform = 'translate(-50%, -100%)';
        break;
      case 'bottom':
        left = Math.max(padding + tooltipWidth / 2, Math.min(window.innerWidth - padding - tooltipWidth / 2, highlightPosition.left + highlightPosition.width / 2));
        top = Math.min(window.innerHeight - padding, highlightPosition.bottom + 20);
        transform = 'translateX(-50%)';
        break;
      case 'left':
        left = Math.max(padding, highlightPosition.left - 20);
        top = Math.max(padding, Math.min(window.innerHeight - padding, highlightPosition.top + highlightPosition.height / 2));
        transform = 'translate(-100%, -50%)';
        break;
      case 'right':
        left = Math.min(window.innerWidth - padding, highlightPosition.right + 20);
        top = Math.max(padding, Math.min(window.innerHeight - padding, highlightPosition.top + highlightPosition.height / 2));
        transform = 'translateY(-50%)';
        break;
      default:
        left = Math.max(padding + tooltipWidth / 2, Math.min(window.innerWidth - padding - tooltipWidth / 2, highlightPosition.left + highlightPosition.width / 2));
        top = Math.min(window.innerHeight - padding, highlightPosition.bottom + 20);
        transform = 'translateX(-50%)';
    }

    return {
      position: 'fixed',
      zIndex: 10002,
      maxWidth: `${tooltipWidth}px`,
      left: `${left}px`,
      top: `${top}px`,
      transform
    };
  };

  // select-memo   dim  
  const shouldHideCanvasDim = step.id === 'select-memo';

  return (
    <>
      {/*   (  ) */}
      {highlightPosition ? (
        <>
          {/*  - select-memo     */}
          {!shouldHideCanvasDim && (
            <div
              style={{
                position: 'fixed',
                top: 0,
                left: 0,
                width: '100vw',
                height: highlightPosition.top,
                backgroundColor: 'rgba(0, 0, 0, 0.6)',
                zIndex: 10001,
                pointerEvents: 'none'
              }}
            />
          )}
          {/*  */}
          <div
            style={{
              position: 'fixed',
              top: highlightPosition.top,
              left: 0,
              width: highlightPosition.left,
              height: highlightPosition.height,
              backgroundColor: 'rgba(0, 0, 0, 0.6)',
              zIndex: 10001,
              pointerEvents: 'none'
            }}
          />
          {/*  - select-memo    */}
          <div
            style={{
              position: 'fixed',
              top: highlightPosition.top,
              left: highlightPosition.right,
              width: window.innerWidth - highlightPosition.right,
              height: highlightPosition.height,
              backgroundColor: 'rgba(0, 0, 0, 0.6)',
              zIndex: 10001,
              pointerEvents: 'none'
            }}
          />
          {/*  - select-memo     */}
          {!shouldHideCanvasDim && (
            <div
              style={{
                position: 'fixed',
                top: highlightPosition.bottom,
                left: 0,
                width: '100vw',
                height: window.innerHeight - highlightPosition.bottom,
                backgroundColor: 'rgba(0, 0, 0, 0.6)',
                zIndex: 10001,
                pointerEvents: 'none'
              }}
            />
          )}
          {/*   */}
          <div
            style={{
              position: 'fixed',
              top: highlightPosition.top - 4,
              left: highlightPosition.left - 4,
              width: highlightPosition.width + 8,
              height: highlightPosition.height + 8,
              border: '4px solid #4A90E2',
              borderRadius: '8px',
              zIndex: 10001,
              pointerEvents: 'none',
              boxShadow: '0 0 20px rgba(74, 144, 226, 0.5)',
              animation: 'pulse 2s ease-in-out infinite'
            }}
          />
        </>
      ) : (
        /*      */
        <div
          style={{
            position: 'fixed',
            top: 0,
            left: 0,
            width: '100vw',
            height: '100vh',
            backgroundColor: 'rgba(0, 0, 0, 0.6)',
            zIndex: 10000,
            pointerEvents: 'auto'
          }}
        />
      )}

      {/*   */}
      <div style={getTooltipStyle()}>
        <div
          style={{
            backgroundColor: '#ffffff',
            borderRadius: '12px',
            padding: '24px',
            boxShadow: '0 8px 32px rgba(0, 0, 0, 0.3)',
            border: '2px solid #4A90E2',
            minWidth: '300px'
          }}
        >
          {/*   */}
          <div style={{ marginBottom: '16px', display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <span style={{ fontSize: '14px', color: '#666', fontWeight: 500 }}>
              {currentStep + 1} / {steps.length}
            </span>
            <button
              onClick={onSkip}
              style={{
                background: 'none',
                border: 'none',
                color: '#999',
                cursor: 'pointer',
                fontSize: '14px',
                padding: '4px 8px'
              }}
            >
              
            </button>
          </div>

          {/*   */}
          <div
            style={{
              width: '100%',
              height: '4px',
              backgroundColor: '#E0E0E0',
              borderRadius: '2px',
              marginBottom: '20px',
              overflow: 'hidden'
            }}
          >
            <div
              style={{
                width: `${((currentStep + 1) / steps.length) * 100}%`,
                height: '100%',
                backgroundColor: '#4A90E2',
                transition: 'width 0.3s ease'
              }}
            />
          </div>

          {/*  */}
          <h3 style={{ margin: '0 0 12px 0', fontSize: '18px', fontWeight: 600, color: '#333' }}>
            {step.title}
          </h3>

          {/*  */}
          <p style={{ margin: '0 0 24px 0', fontSize: '14px', lineHeight: '1.6', color: '#666', whiteSpace: 'pre-line' }}>
            {step.description}
          </p>

          {/*   */}
          <div style={{ display: 'flex', gap: '12px', justifyContent: 'flex-end' }}>
            {isLastStep ? (
              <button
                onClick={onComplete}
                style={{
                  padding: '10px 24px',
                  backgroundColor: '#4A90E2',
                  color: 'white',
                  border: 'none',
                  borderRadius: '6px',
                  cursor: 'pointer',
                  fontSize: '14px',
                  fontWeight: 600,
                  transition: 'background-color 0.2s'
                }}
                onMouseEnter={(e) => (e.currentTarget.style.backgroundColor = '#357ABD')}
                onMouseLeave={(e) => (e.currentTarget.style.backgroundColor = '#4A90E2')}
              >
                
              </button>
            ) : (
              <button
                onClick={canProceed ? onNext : undefined}
                disabled={!canProceed}
                style={{
                  padding: '10px 24px',
                  backgroundColor: canProceed ? '#4A90E2' : '#9CA3AF',
                  color: 'white',
                  border: 'none',
                  borderRadius: '6px',
                  cursor: canProceed ? 'pointer' : 'not-allowed',
                  fontSize: '14px',
                  fontWeight: 600,
                  transition: 'background-color 0.2s',
                  opacity: canProceed ? 1 : 0.6
                }}
                onMouseEnter={(e) => {
                  if (canProceed) {
                    e.currentTarget.style.backgroundColor = '#357ABD';
                  }
                }}
                onMouseLeave={(e) => {
                  if (canProceed) {
                    e.currentTarget.style.backgroundColor = '#4A90E2';
                  }
                }}
              >
                {step.nextButtonText || ''}
              </button>
            )}
          </div>
        </div>
      </div>

      {/* CSS  */}
      <style>
        {`
          @keyframes pulse {
            0%, 100% {
              opacity: 1;
              transform: scale(1);
            }
            50% {
              opacity: 0.8;
              transform: scale(1.02);
            }
          }
        `}
      </style>
    </>
  );
};
