import React, { useState, useEffect } from 'react';
import { SheetsBlock as SheetsBlockType, ContentBlock } from '../../types';
import { 
  parseSheetUrl, 
  getSheetData, 
  convertSheetDataToTable, 
  checkSheetAccess,
  isUserSignedIn,
  startRangeDetection,
  detectRangeFromClipboard
} from '../../utils/googleSheetsAPI';

interface SheetsBlockProps {
  block: SheetsBlockType;
  onUpdate?: (updatedBlock: ContentBlock) => void;
  onDelete?: () => void;
  isSelected?: boolean;
  onSelect?: () => void;
  onCreateTableBlock?: (tableData: { 
    headers: string[], 
    rows: string[][], 
    columns?: any[], 
    cells?: any[] 
  }) => void;
  onCreateNewBlock?: (afterBlockId: string, content: string) => void;
}

const SheetsBlock: React.FC<SheetsBlockProps> = ({
  block,
  onUpdate,
  onDelete,
  isSelected,
  onSelect,
  onCreateTableBlock,
  onCreateNewBlock
}) => {
  const [isEditing, setIsEditing] = useState(!block.url);
  const [tempUrl, setTempUrl] = useState(block.url);
  const [tempTitle, setTempTitle] = useState(block.title || '');
  const [isIframeFocused, setIsIframeFocused] = useState(false);
  const [isExtracting, setIsExtracting] = useState(false);
  const [extractRange, setExtractRange] = useState('A1:Z1000'); //  
  const [showRangeInput, setShowRangeInput] = useState(false);
  const [selectedCell, setSelectedCell] = useState<{row: number, col: number} | null>(null);
  const [rangeStart, setRangeStart] = useState<{row: number, col: number} | null>(null);
  const [rangeEnd, setRangeEnd] = useState<{row: number, col: number} | null>(null);
  const [isListeningForRange, setIsListeningForRange] = useState(false);
  const [rangeDetectionStop, setRangeDetectionStop] = useState<(() => void) | null>(null);
  const [showRangeBuilder, setShowRangeBuilder] = useState(false);
  const [rangeBuilder, setRangeBuilder] = useState({
    startCol: 'A',
    startRow: '1',
    endCol: 'C',
    endRow: '10',
    sheetName: ''
  });
  const iframeRef = React.useRef<HTMLIFrameElement>(null);

  //      
  useEffect(() => {
    return () => {
      if (rangeDetectionStop) {
        rangeDetectionStop();
      }
    };
  }, [rangeDetectionStop]);

  const convertToEmbedUrl = (url: string): string => {
    //   URL embed URL 
    if (url.includes('docs.google.com/spreadsheets')) {
      const matches = url.match(/\/spreadsheets\/d\/([a-zA-Z0-9-_]+)/);
      if (matches && matches[1]) {
        return `https://docs.google.com/spreadsheets/d/${matches[1]}/edit?usp=sharing&rm=minimal&widget=true&headers=false&chrome=false&gridlines=false`;
      }
    }
    return url;
  };

  //     
  const buildRangeString = () => {
    const { startCol, startRow, endCol, endRow, sheetName } = rangeBuilder;
    const range = `${startCol}${startRow}:${endCol}${endRow}`;
    return sheetName ? `${sheetName}!${range}` : range;
  };

  //   
  const applyBuiltRange = () => {
    const newRange = buildRangeString();
    setExtractRange(newRange);
    setShowRangeBuilder(false);
  };

  //   /
  const toggleRangeDetection = async () => {
    if (isListeningForRange) {
      //  
      if (rangeDetectionStop) {
        rangeDetectionStop();
        setRangeDetectionStop(null);
      }
      setIsListeningForRange(false);
    } else {
      //  
      const sheetId = parseSheetUrl(block.url);
      if (!sheetId) {
        alert('   URL .');
        return;
      }

      if (!isUserSignedIn()) {
        alert('   .');
        return;
      }

      setIsListeningForRange(true);
      
      try {
        //     
        const stopFunction = await startRangeDetection(
          sheetId,
          (detectedRange: string, detectedData?: string) => {
            setExtractRange(detectedRange);
            //      
          },
          1500 // 1.5 
        );
        
        setRangeDetectionStop(() => stopFunction);
        
        //   
        setTimeout(() => {
          if (isListeningForRange) {
          }
        }, 500);
        
      } catch (error) {
        console.error('   :', error);
        setIsListeningForRange(false);
        alert('    .');
      }
    }
  };

  const handleSave = () => {
    if (!tempUrl.trim()) return;
    
    const embedUrl = convertToEmbedUrl(tempUrl.trim());
    if (onUpdate) {
      onUpdate({
        ...block,
        url: embedUrl,
        title: tempTitle.trim() || ' ',
        width: block.width || 800,
        height: block.height || 400,
        zoom: block.zoom || 100
      });
    }
    setIsEditing(false);
  };

  const handleCancel = () => {
    if (!block.url && onDelete) {
      onDelete();
    } else {
      setTempUrl(block.url);
      setTempTitle(block.title || '');
      setIsEditing(false);
    }
  };

  const handleWidthChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const newWidth = parseInt(e.target.value);
    if (onUpdate) {
      onUpdate({
        ...block,
        width: newWidth
      });
    }
  };

  const handleHeightChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const newHeight = parseInt(e.target.value);
    if (onUpdate) {
      onUpdate({
        ...block,
        height: newHeight
      });
    }
  };

  const handleExtractData = async () => {
    if (!isUserSignedIn()) {
      alert('   .');
      return;
    }

    if (!block.url) {
      alert('   URL .');
      return;
    }

    const sheetId = parseSheetUrl(block.url);
    if (!sheetId) {
      alert('   URL .');
      return;
    }

    setIsExtracting(true);
    
    try {
      //    
      await checkSheetAccess(sheetId);
      
      //   ( )
      const sheetData = await getSheetData(sheetId, extractRange);
      
      if (!sheetData) {
        alert('    .');
        return;
      }

      //   
      const tableData = convertSheetDataToTable(sheetData);
      
      //    
      if (onCreateTableBlock) {
        onCreateTableBlock(tableData);
        alert('    !');
      }
      
    } catch (error: any) {
      console.error('  :', error);
      alert(error.message || '    .');
    } finally {
      setIsExtracting(false);
    }
  };

  const handleExtractAsText = async () => {
    if (!isUserSignedIn()) {
      alert('   .');
      return;
    }

    if (!block.url) {
      alert('   URL .');
      return;
    }

    const sheetId = parseSheetUrl(block.url);
    if (!sheetId) {
      alert('   URL .');
      return;
    }

    setIsExtracting(true);
    
    try {
      //    
      await checkSheetAccess(sheetId);
      
      //    
      const sheetData = await getSheetData(sheetId, extractRange);
      
      if (!sheetData || !sheetData.values) {
        alert('     .');
        return;
      }

      //   
      const textContent = sheetData.values.map(row => row.join('\t')).join('\n');
      
      //   
      if (onCreateNewBlock) {
        onCreateNewBlock(block.id, textContent);
        alert(` ${extractRange}    !`);
        setShowRangeInput(false); //  
      }
      
    } catch (error: any) {
      console.error('  :', error);
      alert(error.message || '    .');
    } finally {
      setIsExtracting(false);
    }
  };

  // iframe     
  React.useEffect(() => {
    const handleWheel = (e: Event) => {
      if (isIframeFocused) {
        e.preventDefault();
        e.stopPropagation();
        e.stopImmediatePropagation();
      }
    };

    const handleMouseOver = () => setIsIframeFocused(true);
    const handleMouseOut = () => setIsIframeFocused(false);

    const iframeElement = iframeRef.current;
    if (iframeElement) {
      const container = iframeElement.parentElement;
      if (container) {
        //      
        container.addEventListener('wheel', handleWheel, { passive: false, capture: true });
        container.addEventListener('mouseover', handleMouseOver);
        container.addEventListener('mouseout', handleMouseOut);

        //       (RightPanel)
        const rightPanelElement = container.closest('[style*="overflow"]') || container.closest('.right-panel') || document.querySelector('.right-panel');
        if (rightPanelElement && rightPanelElement !== container) {
          rightPanelElement.addEventListener('wheel', handleWheel, { passive: false, capture: true });
        }

        return () => {
          container.removeEventListener('wheel', handleWheel, { capture: true });
          container.removeEventListener('mouseover', handleMouseOver);
          container.removeEventListener('mouseout', handleMouseOut);
          
          if (rightPanelElement && rightPanelElement !== container) {
            rightPanelElement.removeEventListener('wheel', handleWheel, { capture: true });
          }
        };
      }
    }
  }, [isIframeFocused]);

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && (e.ctrlKey || e.metaKey)) {
      handleSave();
    } else if (e.key === 'Escape') {
      handleCancel();
    }
  };

  if (isEditing) {
    return (
      <div
        onClick={onSelect}
        style={{
          margin: '8px 0',
          padding: '16px',
          border: isSelected ? '2px solid #3b82f6' : '1px solid #e5e7eb',
          borderRadius: '8px',
          backgroundColor: isSelected ? '#f0f9ff' : 'white',
          cursor: 'pointer'
        }}
      >
        <div style={{ marginBottom: '12px' }}>
          <input
            type="text"
            placeholder="   ()"
            value={tempTitle}
            onChange={(e) => setTempTitle(e.target.value)}
            onKeyDown={handleKeyDown}
            style={{
              width: '100%',
              padding: '8px 12px',
              border: '1px solid #d1d5db',
              borderRadius: '6px',
              fontSize: '14px',
              marginBottom: '8px'
            }}
          />
          <input
            type="url"
            placeholder="  URL "
            value={tempUrl}
            onChange={(e) => setTempUrl(e.target.value)}
            onKeyDown={handleKeyDown}
            autoFocus
            style={{
              width: '100%',
              padding: '8px 12px',
              border: '1px solid #d1d5db',
              borderRadius: '6px',
              fontSize: '14px'
            }}
          />
        </div>
        <div style={{ display: 'flex', gap: '8px', fontSize: '14px' }}>
          <button
            onClick={handleSave}
            disabled={!tempUrl.trim()}
            style={{
              padding: '6px 12px',
              backgroundColor: tempUrl.trim() ? '#3b82f6' : '#9ca3af',
              color: 'white',
              border: 'none',
              borderRadius: '4px',
              cursor: tempUrl.trim() ? 'pointer' : 'not-allowed',
              fontSize: '14px'
            }}
          >
            
          </button>
          <button
            onClick={handleCancel}
            style={{
              padding: '6px 12px',
              backgroundColor: '#6b7280',
              color: 'white',
              border: 'none',
              borderRadius: '4px',
              cursor: 'pointer',
              fontSize: '14px'
            }}
          >
            
          </button>
        </div>
        <div style={{
          fontSize: '12px',
          color: '#6b7280',
          marginTop: '8px',
          lineHeight: '1.4'
        }}>
                . (   )
        </div>
      </div>
    );
  }

  return (
    <div
      onClick={onSelect}
      style={{
        margin: '8px 0',
        border: isSelected ? '2px solid #3b82f6' : '1px solid #e5e7eb',
        borderRadius: '8px',
        backgroundColor: isSelected ? '#f0f9ff' : 'white',
        cursor: 'pointer',
        overflow: 'hidden'
      }}
    >
      {block.title && (
        <div style={{
          padding: '12px 16px 8px',
          borderBottom: '1px solid #e5e7eb',
          fontSize: '14px',
          fontWeight: '500',
          color: '#374151',
          display: 'flex',
          alignItems: 'center',
          gap: '8px'
        }}>
          <span></span>
          {block.title}
          <div style={{ marginLeft: 'auto', display: 'flex', gap: '8px' }}>
            <button
              onClick={(e) => {
                e.stopPropagation();
                setShowRangeInput(!showRangeInput);
              }}
              style={{
                padding: '4px 8px',
                fontSize: '12px',
                color: '#16a34a',
                backgroundColor: 'transparent',
                border: '1px solid #d1d5db',
                borderRadius: '4px',
                cursor: 'pointer'
              }}
            >
               
            </button>
            <button
              onClick={(e) => {
                e.stopPropagation();
                handleExtractData();
              }}
              disabled={isExtracting}
              style={{
                padding: '4px 8px',
                fontSize: '12px',
                color: isExtracting ? '#9ca3af' : '#16a34a',
                backgroundColor: 'transparent',
                border: '1px solid #d1d5db',
                borderRadius: '4px',
                cursor: isExtracting ? 'not-allowed' : 'pointer'
              }}
            >
              {isExtracting ? ' ...' : ' '}
            </button>
            <button
              onClick={(e) => {
                e.stopPropagation();
                setIsEditing(true);
              }}
              style={{
                padding: '4px 8px',
                fontSize: '12px',
                color: '#6b7280',
                backgroundColor: 'transparent',
                border: '1px solid #d1d5db',
                borderRadius: '4px',
                cursor: 'pointer'
              }}
            >
              
            </button>
          </div>
        </div>
      )}
      
      {/*    */}
      <div style={{
        padding: '12px 16px',
        backgroundColor: '#f9fafb',
        borderBottom: '1px solid #e5e7eb',
        fontSize: '12px'
      }}>
        <div style={{ display: 'flex', alignItems: 'center', gap: '16px', marginBottom: '8px' }}>
          <div style={{ display: 'flex', alignItems: 'center', gap: '8px', minWidth: '120px' }}>
            <span>:</span>
            <input
              type="range"
              min="300"
              max="2000"
              value={block.width || 800}
              onChange={handleWidthChange}
              style={{ flex: 1 }}
            />
            <span style={{ minWidth: '40px', textAlign: 'right' }}>{block.width || 800}px</span>
          </div>
          <div style={{ display: 'flex', alignItems: 'center', gap: '8px', minWidth: '120px' }}>
            <span>:</span>
            <input
              type="range"
              min="200"
              max="1200"
              value={block.height || 400}
              onChange={handleHeightChange}
              style={{ flex: 1 }}
            />
            <span style={{ minWidth: '40px', textAlign: 'right' }}>{block.height || 400}px</span>
          </div>
        </div>
      </div>
      
      <div 
        style={{
          position: 'relative',
          width: `${block.width || 800}px`,
          height: `${block.height || 400}px`,
          backgroundColor: '#f9fafb',
          overflow: 'hidden'
        }}
        onMouseEnter={() => setIsIframeFocused(true)}
        onMouseLeave={() => setIsIframeFocused(false)}
        onWheel={(e) => {
          //    (React  )
          if (isIframeFocused) {
            e.preventDefault();
            e.stopPropagation();
          }
        }}
      >
        <iframe
          ref={iframeRef}
          src={block.url}
          title={block.title || ' '}
          width={`${block.width || 800}px`}
          height={`${block.height || 400}px`}
          style={{
            border: 'none',
            display: 'block'
          }}
          sandbox="allow-scripts allow-same-origin allow-forms allow-popups"
        />
      </div>
      
      {/*    */}
      {showRangeInput && (
        <div style={{
          padding: '12px 16px',
          backgroundColor: '#f8f9fa',
          borderTop: '1px solid #e5e7eb',
          borderBottom: '1px solid #e5e7eb'
        }}>
          <div style={{ marginBottom: '12px', fontSize: '14px', fontWeight: '500' }}>
                
          </div>
          
          {/*     */}
          <div style={{ marginBottom: '12px' }}>
            <div style={{ fontSize: '12px', color: '#6b7280', marginBottom: '6px' }}> :</div>
            <div style={{ display: 'flex', gap: '6px', flexWrap: 'wrap' }}>
              {[
                { label: ' (A1:Z1000)', value: 'A1:Z1000' },
                { label: ' 10 (A1:Z10)', value: 'A1:Z10' },
                { label: '   (A:A)', value: 'A:A' },
                { label: ' (A1:Z1)', value: 'A1:Z1' },
                { label: 'A-E  (A:E)', value: 'A:E' },
                { label: '100 (A1:Z100)', value: 'A1:Z100' }
              ].map((preset) => (
                <button
                  key={preset.value}
                  onClick={(e) => {
                    e.stopPropagation();
                    setExtractRange(preset.value);
                  }}
                  style={{
                    padding: '3px 8px',
                    fontSize: '11px',
                    backgroundColor: extractRange === preset.value ? '#e3f2fd' : 'white',
                    color: extractRange === preset.value ? '#1976d2' : '#666',
                    border: extractRange === preset.value ? '1px solid #90caf9' : '1px solid #d1d5db',
                    borderRadius: '4px',
                    cursor: 'pointer'
                  }}
                >
                  {preset.label}
                </button>
              ))}
            </div>
          </div>
          
          {/*     */}
          <div style={{ marginBottom: '12px' }}>
            <div style={{ fontSize: '12px', color: '#6b7280', marginBottom: '6px' }}>  :</div>
            <div style={{ display: 'flex', gap: '8px', alignItems: 'center' }}>
              <input
                type="text"
                value={extractRange}
                onChange={(e) => setExtractRange(e.target.value)}
                onKeyDown={(e) => {
                  //      
                  e.stopPropagation();
                  if (e.key === 'Enter') {
                    handleExtractAsText();
                  }
                }}
                onClick={(e) => e.stopPropagation()}
                placeholder=": A1:C10  Sheet1!A1:B5"
                style={{
                  padding: '6px 10px',
                  border: '1px solid #d1d5db',
                  borderRadius: '4px',
                  fontSize: '12px',
                  flex: 1,
                  fontFamily: 'monospace'
                }}
              />
              <button
                onClick={(e) => {
                  e.stopPropagation();
                  setShowRangeBuilder(!showRangeBuilder);
                }}
                style={{
                  padding: '6px 10px',
                  fontSize: '11px',
                  backgroundColor: showRangeBuilder ? '#e3f2fd' : '#f8f9fa',
                  color: showRangeBuilder ? '#1976d2' : '#666',
                  border: '1px solid #d1d5db',
                  borderRadius: '4px',
                  cursor: 'pointer',
                  whiteSpace: 'nowrap'
                }}
              >
                 
              </button>
              <button
                onClick={(e) => {
                  e.stopPropagation();
                  toggleRangeDetection();
                }}
                style={{
                  padding: '6px 10px',
                  fontSize: '11px',
                  backgroundColor: isListeningForRange ? '#fef3c7' : '#f8f9fa',
                  color: isListeningForRange ? '#d97706' : '#666',
                  border: isListeningForRange ? '1px solid #fbbf24' : '1px solid #d1d5db',
                  borderRadius: '4px',
                  cursor: 'pointer',
                  whiteSpace: 'nowrap'
                }}
              >
                 {isListeningForRange ? '...' : ''}
              </button>
              <button
                onClick={async (e) => {
                  e.stopPropagation();
                  try {
                    const result = await detectRangeFromClipboard();
                    if (result) {
                      setExtractRange(result.range);
                      alert(`  : ${result.range}`);
                    } else {
                      alert('      .\n\n    (Ctrl+C)    .');
                    }
                  } catch (error) {
                    console.error('   :', error);
                    alert('   .   .');
                  }
                }}
                style={{
                  padding: '6px 10px',
                  fontSize: '11px',
                  backgroundColor: '#f8f9fa',
                  color: '#666',
                  border: '1px solid #d1d5db',
                  borderRadius: '4px',
                  cursor: 'pointer',
                  whiteSpace: 'nowrap'
                }}
              >
                 
              </button>
            </div>
          </div>
          
          {/*    */}
          {showRangeBuilder && (
            <div style={{
              marginBottom: '12px',
              padding: '12px',
              backgroundColor: '#f0f9ff',
              border: '1px solid #93c5fd',
              borderRadius: '6px',
              fontSize: '12px'
            }}>
              <div style={{ fontWeight: '500', marginBottom: '8px', color: '#1e40af' }}>   </div>
              
              {/*   () */}
              <div style={{ marginBottom: '8px' }}>
                <label style={{ display: 'block', marginBottom: '4px', color: '#374151' }}>  ():</label>
                <input
                  type="text"
                  value={rangeBuilder.sheetName}
                  onChange={(e) => setRangeBuilder({...rangeBuilder, sheetName: e.target.value})}
                  onKeyDown={(e) => e.stopPropagation()}
                  placeholder="Sheet1,  ..."
                  style={{
                    width: '100%',
                    padding: '4px 8px',
                    border: '1px solid #d1d5db',
                    borderRadius: '4px',
                    fontSize: '11px'
                  }}
                />
              </div>
              
              {/*    */}
              <div style={{ display: 'grid', gridTemplateColumns: '1fr auto 1fr', gap: '8px', alignItems: 'center', marginBottom: '8px' }}>
                <div>
                  <label style={{ display: 'block', marginBottom: '4px', color: '#374151' }}> :</label>
                  <div style={{ display: 'flex', gap: '4px' }}>
                    <input
                      type="text"
                      value={rangeBuilder.startCol}
                      onChange={(e) => setRangeBuilder({...rangeBuilder, startCol: e.target.value.toUpperCase()})}
                      onKeyDown={(e) => e.stopPropagation()}
                      placeholder="A"
                      style={{
                        width: '40px',
                        padding: '4px 8px',
                        border: '1px solid #d1d5db',
                        borderRadius: '4px',
                        fontSize: '11px',
                        textAlign: 'center',
                        fontFamily: 'monospace'
                      }}
                    />
                    <input
                      type="number"
                      value={rangeBuilder.startRow}
                      onChange={(e) => setRangeBuilder({...rangeBuilder, startRow: e.target.value})}
                      onKeyDown={(e) => e.stopPropagation()}
                      min="1"
                      placeholder="1"
                      style={{
                        width: '50px',
                        padding: '4px 8px',
                        border: '1px solid #d1d5db',
                        borderRadius: '4px',
                        fontSize: '11px',
                        textAlign: 'center',
                        fontFamily: 'monospace'
                      }}
                    />
                  </div>
                </div>
                
                <div style={{ textAlign: 'center', color: '#6b7280', fontSize: '14px', padding: '0 8px' }}>:</div>
                
                <div>
                  <label style={{ display: 'block', marginBottom: '4px', color: '#374151' }}> :</label>
                  <div style={{ display: 'flex', gap: '4px' }}>
                    <input
                      type="text"
                      value={rangeBuilder.endCol}
                      onChange={(e) => setRangeBuilder({...rangeBuilder, endCol: e.target.value.toUpperCase()})}
                      onKeyDown={(e) => e.stopPropagation()}
                      placeholder="C"
                      style={{
                        width: '40px',
                        padding: '4px 8px',
                        border: '1px solid #d1d5db',
                        borderRadius: '4px',
                        fontSize: '11px',
                        textAlign: 'center',
                        fontFamily: 'monospace'
                      }}
                    />
                    <input
                      type="number"
                      value={rangeBuilder.endRow}
                      onChange={(e) => setRangeBuilder({...rangeBuilder, endRow: e.target.value})}
                      onKeyDown={(e) => e.stopPropagation()}
                      min="1"
                      placeholder="10"
                      style={{
                        width: '50px',
                        padding: '4px 8px',
                        border: '1px solid #d1d5db',
                        borderRadius: '4px',
                        fontSize: '11px',
                        textAlign: 'center',
                        fontFamily: 'monospace'
                      }}
                    />
                  </div>
                </div>
              </div>
              
              {/*  */}
              <div style={{
                padding: '6px 8px',
                backgroundColor: '#e0f2fe',
                border: '1px solid #84d3ff',
                borderRadius: '4px',
                marginBottom: '8px',
                fontFamily: 'monospace',
                fontSize: '11px',
                color: '#0c4a6e'
              }}>
                : <strong>{buildRangeString()}</strong>
              </div>
              
              {/*  */}
              <div style={{ display: 'flex', gap: '6px', justifyContent: 'flex-end' }}>
                <button
                  onClick={(e) => {
                    e.stopPropagation();
                    applyBuiltRange();
                  }}
                  style={{
                    padding: '4px 8px',
                    fontSize: '11px',
                    backgroundColor: '#2563eb',
                    color: 'white',
                    border: 'none',
                    borderRadius: '4px',
                    cursor: 'pointer'
                  }}
                >
                  
                </button>
                <button
                  onClick={(e) => {
                    e.stopPropagation();
                    setShowRangeBuilder(false);
                  }}
                  style={{
                    padding: '4px 8px',
                    fontSize: '11px',
                    backgroundColor: '#6b7280',
                    color: 'white',
                    border: 'none',
                    borderRadius: '4px',
                    cursor: 'pointer'
                  }}
                >
                  
                </button>
              </div>
            </div>
          )}
          
          {/*    */}
          {isListeningForRange && (
            <div style={{
              marginBottom: '12px',
              padding: '12px',
              backgroundColor: '#fef3c7',
              border: '1px solid #fbbf24',
              borderRadius: '6px',
              fontSize: '11px'
            }}>
              <div style={{ fontWeight: '500', marginBottom: '8px', color: '#d97706', fontSize: '12px' }}>
                    !
              </div>
              
              <div style={{ marginBottom: '10px' }}>
                <div style={{ fontWeight: '500', marginBottom: '4px', color: '#b45309' }}>   (3):</div>
                <div style={{ paddingLeft: '12px', lineHeight: '1.4' }}>
                  <div style={{ marginBottom: '2px' }}>
                    <span style={{ fontWeight: '500', color: '#92400e' }}>1:</span>      <strong></strong> 
                  </div>
                  <div style={{ marginBottom: '2px' }}>
                    <span style={{ fontWeight: '500', color: '#92400e' }}>2:</span> <strong>Ctrl+C</strong> (Mac: Cmd+C)   
                  </div>
                  <div style={{ marginBottom: '2px' }}>
                    <span style={{ fontWeight: '500', color: '#92400e' }}>3:</span>      
                  </div>
                </div>
              </div>

              <div style={{ 
                marginBottom: '10px', 
                padding: '6px 8px',
                backgroundColor: '#fffbeb',
                border: '1px solid #fed7aa',
                borderRadius: '4px',
                fontSize: '10px'
              }}>
                <div style={{ fontWeight: '500', marginBottom: '3px', color: '#ea580c' }}>   :</div>
                <div style={{ color: '#9a3412', paddingLeft: '8px' }}>
                       ( )
                     
                     CSV 
                     
                </div>
              </div>
              
              <div style={{ 
                marginBottom: '8px', 
                padding: '6px 8px',
                backgroundColor: '#e0f2fe',
                border: '1px solid #81d4fa',
                borderRadius: '4px',
                fontSize: '10px',
                color: '#01579b'
              }}>
                 <strong>Pro :</strong>   /    /        !
              </div>
              
              <div style={{ display: 'flex', gap: '6px', justifyContent: 'center' }}>
                <button
                  onClick={(e) => {
                    e.stopPropagation();
                    toggleRangeDetection();
                  }}
                  style={{
                    padding: '5px 12px',
                    fontSize: '11px',
                    backgroundColor: '#dc2626',
                    color: 'white',
                    border: 'none',
                    borderRadius: '4px',
                    cursor: 'pointer',
                    fontWeight: '500'
                  }}
                >
                    
                </button>
              </div>
            </div>
          )}
          
          {/*   */}
          <div style={{ display: 'flex', gap: '8px', justifyContent: 'flex-end' }}>
            <button
              onClick={(e) => {
                e.stopPropagation();
                handleExtractData();
              }}
              disabled={isExtracting}
              style={{
                padding: '6px 12px',
                fontSize: '12px',
                color: isExtracting ? '#9ca3af' : '#16a34a',
                backgroundColor: isExtracting ? 'transparent' : '#f0f9ff',
                border: '1px solid #d1d5db',
                borderRadius: '4px',
                cursor: isExtracting ? 'not-allowed' : 'pointer'
              }}
            >
                
            </button>
            <button
              onClick={(e) => {
                e.stopPropagation();
                handleExtractAsText();
              }}
              disabled={isExtracting}
              style={{
                padding: '6px 12px',
                fontSize: '12px',
                color: isExtracting ? '#9ca3af' : '#2563eb',
                backgroundColor: isExtracting ? 'transparent' : '#f8f9fa',
                border: '1px solid #d1d5db',
                borderRadius: '4px',
                cursor: isExtracting ? 'not-allowed' : 'pointer'
              }}
            >
                
            </button>
            <button
              onClick={(e) => {
                e.stopPropagation();
                setShowRangeInput(false);
              }}
              style={{
                padding: '6px 12px',
                fontSize: '12px',
                color: '#6b7280',
                backgroundColor: 'transparent',
                border: '1px solid #d1d5db',
                borderRadius: '4px',
                cursor: 'pointer'
              }}
            >
              
            </button>
          </div>
          
          <div style={{ fontSize: '11px', color: '#6b7280', marginTop: '8px' }}>
              : A1:C10 (A1 C10), A:C (A C ), Sheet1!A1:B5 (  )
          </div>
        </div>
      )}
      
      {!block.title && (
        <div style={{
          padding: '8px 16px',
          borderTop: '1px solid #e5e7eb',
          fontSize: '12px',
          color: '#6b7280',
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center'
        }}>
           
          <div style={{ marginLeft: 'auto', display: 'flex', gap: '8px' }}>
            <button
              onClick={(e) => {
                e.stopPropagation();
                handleExtractData();
              }}
              disabled={isExtracting}
              style={{
                padding: '4px 8px',
                fontSize: '12px',
                color: isExtracting ? '#9ca3af' : '#16a34a',
                backgroundColor: 'transparent',
                border: '1px solid #d1d5db',
                borderRadius: '4px',
                cursor: isExtracting ? 'not-allowed' : 'pointer'
              }}
            >
              {isExtracting ? ' ...' : ' '}
            </button>
            <button
              onClick={(e) => {
                e.stopPropagation();
                setIsEditing(true);
              }}
              style={{
                padding: '4px 8px',
                fontSize: '12px',
                color: '#6b7280',
                backgroundColor: 'transparent',
                border: '1px solid #d1d5db',
                borderRadius: '4px',
                cursor: 'pointer'
              }}
            >
              
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default SheetsBlock;