import { ImportanceLevel } from '../types';

//     
export const getImportanceStyle = (level?: ImportanceLevel) => {
  if (!level || level === 'none') return {};

  switch (level) {
    case 'critical':
      return { backgroundColor: '#ffcdd2', borderLeft: '3px solid #f44336' }; //   - 
    case 'important':
      return { backgroundColor: '#ffcc80', borderLeft: '3px solid #ff9800' }; //   - 
    case 'opinion':
      return { backgroundColor: '#e1bee7', borderLeft: '3px solid #9c27b0' }; //   - 
    case 'reference':
      return { backgroundColor: '#81d4fa', borderLeft: '3px solid #2196f3' }; //   - 
    case 'question':
      return { backgroundColor: '#fff59d', borderLeft: '3px solid #fbc02d' }; //   - 
    case 'idea':
      return { backgroundColor: '#c8e6c9', borderLeft: '3px solid #4caf50' }; //   - 
    case 'data':
      return { backgroundColor: '#ffab91', borderLeft: '3px solid #ff5722' }; //   - 
    default:
      return {};
  }
};

export const IMPORTANCE_LABELS: Record<ImportanceLevel, string> = {
  critical: '',
  important: '',
  opinion: '',
  reference: '',
  question: '',
  idea: '',
  data: '',
  none: ' '
};

//    ( )
export const IMPORTANCE_COLORS: Record<ImportanceLevel, string> = {
  critical: '#ffcdd2',
  important: '#ffcc80',
  opinion: '#e1bee7',
  reference: '#81d4fa',
  question: '#fff59d',
  idea: '#c8e6c9',
  data: '#ffab91',
  none: '#ffffff'
};

//      
export const shouldShowBlock = (
  blockImportance?: ImportanceLevel,
  activeFilters?: Set<ImportanceLevel>,
  showGeneralContent?: boolean
): boolean => {
  //      
  if (!activeFilters && showGeneralContent !== false) {
    return true;
  }

  //     
  if (blockImportance && blockImportance !== 'none') {
    //      
    return activeFilters ? activeFilters.has(blockImportance) : true;
  }

  //      ( )
  return showGeneralContent !== false;
};
