import React, { useState } from 'react';

interface FormulaHelperProps {
  onInsertFormula: (formula: string) => void;
}

const FormulaHelper: React.FC<FormulaHelperProps> = ({ onInsertFormula }) => {
  const [selectedCategory, setSelectedCategory] = useState<string>('basic');

  const formulaCategories = {
    basic: {
      name: ' ',
      formulas: [
        { name: 'SUM', syntax: 'SUM(@1, @2, ...)', desc: ' ' },
        { name: 'AVERAGE', syntax: 'AVERAGE(@1, @2, ...)', desc: ' ' },
        { name: 'MAX', syntax: 'MAX(@1, @2, ...)', desc: '' },
        { name: 'MIN', syntax: 'MIN(@1, @2, ...)', desc: '' },
      ]
    },
    process: {
      name: '',
      formulas: [
        { name: 'PROGRESS', syntax: 'PROGRESS(@, @)', desc: ' (%)' },
        { name: 'STATUS', syntax: 'STATUS(@, "", "", "")', desc: ' ' },
        { name: 'DEADLINE', syntax: 'DEADLINE("2024-01-01", 30)', desc: ' ' },
        { name: 'WORKDAYS', syntax: 'WORKDAYS("2024-01-01", "2024-01-31")', desc: ' ' },
        { name: 'APPROVAL', syntax: 'APPROVAL(@, @)', desc: ' ' },
      ]
    },
    data: {
      name: ' ',
      formulas: [
        { name: 'LATEST', syntax: 'LATEST(@)', desc: ' ' },
        { name: 'PREVIOUS', syntax: 'PREVIOUS(@, 7)', desc: ' ' },
        { name: 'SUM_BY', syntax: 'SUM_BY(@, @, "")', desc: ' ' },
      ]
    },
    logic: {
      name: ' ',
      formulas: [
        { name: 'IF', syntax: 'IF(@ > 100, "", "")', desc: ' ' },
        { name: 'COUNT_IF', syntax: 'COUNT_IF(@, "")', desc: ' ' },
      ]
    }
  };

  const handleFormulaClick = (syntax: string) => {
    onInsertFormula(syntax);
  };

  return (
    <div style={{
      position: 'absolute',
      top: '100%',
      left: 0,
      right: 0,
      backgroundColor: 'white',
      border: '1px solid #ccc',
      borderRadius: '8px',
      boxShadow: '0 4px 12px rgba(0,0,0,0.15)',
      zIndex: 1000,
      maxHeight: '400px',
      overflow: 'hidden',
      display: 'flex'
    }}>
      {/* Category tabs */}
      <div style={{
        width: '120px',
        borderRight: '1px solid #eee',
        backgroundColor: '#f8f9fa'
      }}>
        <div style={{
          padding: '8px 12px',
          fontSize: '12px',
          fontWeight: 'bold',
          color: '#666',
          borderBottom: '1px solid #eee'
        }}>
           
        </div>
        {Object.entries(formulaCategories).map(([key, category]) => (
          <div
            key={key}
            style={{
              padding: '8px 12px',
              fontSize: '12px',
              cursor: 'pointer',
              backgroundColor: selectedCategory === key ? '#007bff' : 'transparent',
              color: selectedCategory === key ? 'white' : '#333'
            }}
            onClick={() => setSelectedCategory(key)}
          >
            {category.name}
          </div>
        ))}
      </div>

      {/* Formula list */}
      <div style={{ flex: 1, maxHeight: '400px', overflowY: 'auto' }}>
        <div style={{
          padding: '8px 12px',
          fontSize: '12px',
          fontWeight: 'bold',
          color: '#666',
          borderBottom: '1px solid #eee',
          backgroundColor: '#f8f9fa'
        }}>
          {formulaCategories[selectedCategory as keyof typeof formulaCategories].name} 
        </div>
        
        {formulaCategories[selectedCategory as keyof typeof formulaCategories].formulas.map((formula, index) => (
          <div
            key={index}
            style={{
              padding: '12px',
              borderBottom: '1px solid #f0f0f0',
              cursor: 'pointer'
            }}
            onClick={() => handleFormulaClick(formula.syntax)}
            onMouseEnter={(e) => {
              (e.target as HTMLElement).style.backgroundColor = '#f5f5f5';
            }}
            onMouseLeave={(e) => {
              (e.target as HTMLElement).style.backgroundColor = 'white';
            }}
          >
            <div style={{
              fontWeight: 'bold',
              fontSize: '13px',
              marginBottom: '4px',
              color: '#007bff'
            }}>
              {formula.name}
            </div>
            <div style={{
              fontSize: '11px',
              fontFamily: 'monospace',
              backgroundColor: '#f8f9fa',
              padding: '4px 6px',
              borderRadius: '3px',
              marginBottom: '4px',
              color: '#666'
            }}>
              {formula.syntax}
            </div>
            <div style={{
              fontSize: '11px',
              color: '#666'
            }}>
              {formula.desc}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default FormulaHelper;