import React, { useState, useEffect } from 'react';
import { GOOGLE_CONFIG } from '../config/google';
import { updateGoogleAuthState } from '../utils/googleSheetsAPI';
import styles from '../scss/components/GoogleAuth.module.scss';

// Google API  GIS  
declare global {
  interface Window {
    gapi: any;
    google: any;
    googleServicesLoaded: boolean;
    initGoogleServices: () => void;
  }
}

declare const gapi: any;
declare const google: any;

interface GoogleAuthProps {
  onAuthSuccess: (isSignedIn: boolean) => void;
}

const GoogleAuth: React.FC<GoogleAuthProps> = ({ onAuthSuccess }) => {
  const [isSignedIn, setIsSignedIn] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [accessToken, setAccessToken] = useState<string | null>(null);
  const [userProfile, setUserProfile] = useState<any>(null);

  // localStorage    
  const checkExistingAuth = () => {
    try {
      const storedToken = localStorage.getItem('google_access_token');
      const storedProfile = localStorage.getItem('google_user_profile');
      const tokenExpiry = localStorage.getItem('google_token_expiry');
      
      
      if (storedToken && tokenExpiry) {
        const now = new Date().getTime();
        const expiryTime = parseInt(tokenExpiry);
        
        if (now < expiryTime) {
          //   
          setAccessToken(storedToken);
          setIsSignedIn(true);
          
          if (storedProfile) {
            setUserProfile(JSON.parse(storedProfile));
          }
          
          //   
          updateGoogleAuthState({
            isSignedIn: true,
            accessToken: storedToken,
            userProfile: storedProfile ? JSON.parse(storedProfile) : null
          });
          
          onAuthSuccess(true);
          return true;
        } else {
          //  
          localStorage.removeItem('google_access_token');
          localStorage.removeItem('google_user_profile');
          localStorage.removeItem('google_token_expiry');
        }
      }
    } catch (error) {
      console.error('     :', error);
    }
    
    return false;
  };

  useEffect(() => {
    const initializeGoogleServices = async () => {
      try {
        
        //     
        if (checkExistingAuth()) {
        }
        
        // Google API GIS  
        if (!window.gapi || !window.google) {
          console.error('Google API  GIS  ');
          setIsLoading(false);
          return;
        }

        // Google API Client  (Sheets API)
        await new Promise<void>((resolve, reject) => {
          gapi.load('client', {
            callback: resolve,
            onerror: reject
          });
        });

        await gapi.client.init({
          apiKey: '', // API Key  (OAuth )
          discoveryDocs: GOOGLE_CONFIG.DISCOVERY_DOCS,
        });

        //   
        if (!GOOGLE_CONFIG.CLIENT_ID || GOOGLE_CONFIG.CLIENT_ID.includes('your-client-id')) {
          throw new Error(' Google Client ID  ');
        }

        
        setIsLoading(false);
      } catch (error) {
        console.error('=== Google Services   ===');
        console.error(' :', error);
        setIsLoading(false);
      }
    };

    const waitForGoogleServices = () => {
      
      if (window.googleServicesLoaded) {
        initializeGoogleServices();
      } else if (window.gapi && window.google) {
        initializeGoogleServices();
      } else {
        
        const handleGoogleReady = () => {
          window.removeEventListener('googleReady', handleGoogleReady);
          initializeGoogleServices();
        };
        
        window.addEventListener('googleReady', handleGoogleReady);
        
        //   (15 )
        setTimeout(() => {
          if (!window.gapi || !window.google) {
            console.error('Google Services   ');
            window.removeEventListener('googleReady', handleGoogleReady);
            setIsLoading(false);
          } else {
            window.removeEventListener('googleReady', handleGoogleReady);
            initializeGoogleServices();
          }
        }, 15000);
      }
    };
    
    waitForGoogleServices();
  }, [onAuthSuccess]);

  const handleSignIn = async () => {
    
    if (!window.google) {
      console.error('Google Identity Services  ');
      alert('Google   .  .');
      return;
    }

    try {
      
      const client = window.google.accounts.oauth2.initTokenClient({
        client_id: GOOGLE_CONFIG.CLIENT_ID,
        scope: GOOGLE_CONFIG.SCOPES,
        callback: (tokenResponse: any) => {
          
          if (tokenResponse.access_token) {
            //  localStorage  (1 )
            const expiryTime = new Date().getTime() + (3600 * 1000); // 1 
            localStorage.setItem('google_access_token', tokenResponse.access_token);
            localStorage.setItem('google_token_expiry', expiryTime.toString());
            
            setAccessToken(tokenResponse.access_token);
            setIsSignedIn(true);
            onAuthSuccess(true);
            
            //   
            updateGoogleAuthState({
              isSignedIn: true,
              accessToken: tokenResponse.access_token
            });
            
            //   
            fetchUserProfile(tokenResponse.access_token);
            
          } else {
            console.error('   ');
            alert(' .');
          }
        },
        error_callback: (error: any) => {
          console.error('OAuth :', error);
          alert(` : ${error.type || error.message || '   '}`);
        }
      });
      
      //   
      client.requestAccessToken();
      
    } catch (error: any) {
      console.error('===   ===');
      console.error(':', error);
      alert(`  : ${error.message || error}`);
    }
  };

  const fetchUserProfile = async (token: string) => {
    try {
      const response = await fetch('https://www.googleapis.com/oauth2/v2/userinfo', {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });
      
      if (response.ok) {
        const profile = await response.json();
        
        //  localStorage 
        localStorage.setItem('google_user_profile', JSON.stringify(profile));
        
        setUserProfile(profile);
        
        //    
        updateGoogleAuthState({
          isSignedIn: true,
          accessToken: token,
          userProfile: profile
        });
      }
    } catch (error) {
      console.error('   :', error);
    }
  };

  const handleSignOut = async () => {
    try {
      
      //  
      if (accessToken && window.google) {
        window.google.accounts.oauth2.revoke(accessToken);
      }
      
      // localStorage 
      localStorage.removeItem('google_access_token');
      localStorage.removeItem('google_user_profile');
      localStorage.removeItem('google_token_expiry');
      
      //  
      setAccessToken(null);
      setIsSignedIn(false);
      setUserProfile(null);
      onAuthSuccess(false);
      
      //   
      updateGoogleAuthState({
        isSignedIn: false,
        accessToken: null,
        userProfile: null
      });
      
    } catch (error) {
      console.error(' :', error);
    }
  };

  if (isLoading) {
    return (
      <div style={{
        padding: '12px 16px',
        backgroundColor: '#f3f4f6',
        borderRadius: '6px',
        fontSize: '14px',
        color: '#6b7280'
      }}>
         API  ...
      </div>
    );
  }

  return (
    <div style={{
      padding: '16px',
      backgroundColor: '#f9fafb',
      borderRadius: '8px',
      border: '1px solid #e5e7eb'
    }}>
      {isSignedIn ? (
        <div>
          <div style={{
            display: 'flex',
            alignItems: 'center',
            gap: '8px',
            marginBottom: '12px'
          }}>
            <span style={{ color: '#16a34a', fontSize: '18px' }}></span>
            <span style={{ color: '#16a34a', fontWeight: '500' }}>
              {userProfile ? `${userProfile.name} ` : '  '}
            </span>
          </div>
          <div style={{ fontSize: '12px', color: '#6b7280', marginBottom: '12px' }}>
                  .
          </div>
          <button
            onClick={handleSignOut}
            style={{
              padding: '8px 16px',
              backgroundColor: '#ef4444',
              color: 'white',
              border: 'none',
              borderRadius: '6px',
              fontSize: '14px',
              cursor: 'pointer'
            }}
          >
             
          </button>
        </div>
      ) : (
        <div>
          <div style={{
            display: 'flex',
            alignItems: 'center',
            gap: '8px',
            marginBottom: '12px'
          }}>
            <span style={{ fontSize: '18px' }}></span>
            <span style={{ fontWeight: '500' }}>
                
            </span>
          </div>
          <div style={{ fontSize: '12px', color: '#6b7280', marginBottom: '12px' }}>
                 .
          </div>
          <button
            onClick={handleSignIn}
            style={{
              padding: '8px 16px',
              backgroundColor: '#4285f4',
              color: 'white',
              border: 'none',
              borderRadius: '6px',
              fontSize: '14px',
              cursor: 'pointer',
              display: 'flex',
              alignItems: 'center',
              gap: '8px'
            }}
          >
            <span>G</span>
             
          </button>
        </div>
      )}
    </div>
  );
};

export default GoogleAuth;